<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process170"></a>

<p><br></p>
<p><span class="rvts17">淹没的悲痛（</span><span class="rvts50">Drowing Her Sorrow</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:201,Y:751</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：阿伯维泽船长（</span><span class="rvts20">Captain Ablewether</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">尝试说服阿伯维泽船长停止纠缠房间里的商人</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1173.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">在酒馆上层，有一间屋子里的一个女商人头脑嗡嗡叫，原因是有一个鬼魂一直缠绕着她（使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">可以发现鬼魂）。</span>
<br><span class="rvts21">女人恳求帮他解除困扰，并标明了地图上货船沉没的地方。与鬼魂对话，尝试说服他停止纠缠。不过大多数情况下会以失败告终。</span>
<br><span class="rvts21">如果队伍里有矮人野兽（</span><span class="rvts20">Beast</span><span class="rvts21">），那么可以使用特定的语言让鬼魂平息下来。或者当获得</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">技能后，杀掉鬼魂，也可关闭该任务。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1174.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">另外，女人所说的宝物（船长的罗盘项链）其实位于失事洞穴尽头出口处的一艘沉没的货船里（拥有死亡迷雾）。可以使用不死族角色或利用传送技能，把该项链收入囊中。</span>
<br><span class="rvts21">但是该项链与该任务无关。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



